import React from "react";
import { Button, Container, Row, Col, Navbar } from "react-bootstrap";
import Footer from "./Footer";
import NavbarComponent from "./Navbar";
import "../Login.css";


function About() {
    document.title = "About Us"
  return (
    <div className="about__background">
    <NavbarComponent></NavbarComponent>
        <Container>
        <Row>
          <Col md={6}>
                  <div className="container">
        <h1>OUR MISSION</h1>
        <p style={{fontSize:"16px"}}>
        Be More.<br/>
        We are a global artificial grass manufacturer that takes corporate social responsibility seriously.
        Royal Turf's mission is to enhance global land use and positively impact society through the use of synthetic turf.
        </p>
      </div>
          </Col>
          <Col md={6}>
                <div className="container">
        <h1>OUR VIBE</h1>
        <p style={{fontSize:"16px"}}>
          “Sometimes having fun with your friends is all the therapy you need.”
        </p>
      </div>
          </Col>
        </Row>
        <Row>
          <Col md={6}>
                  <div className="container">
        <h1>OUR PROMISE</h1>
        <p style={{fontSize:"16px"}}>
          When it comes to artificial turf sports fields, Royal Turf is the most trusted brand in the industry. Whether its Football, Cricket or any other sport, Royal Turf fields provide athletes with the safety and performance they need to perform at their best, while giving field owners the durability they want to maximize the value of their investment.
        </p>
      </div>
          </Col>
          <Col md={6}>
                <div className="container">
        <h1>OUR ESSENCE</h1>
        <p style={{fontSize:"16px"}}>
          Treating audiences with respect <br/>
          Customers aren’t just numbers; they’re real people with individual needs.
        </p>
      </div>
          </Col>
        </Row>
      </Container>
      <Footer></Footer>
    </div>
  );
}

export default About;
