import React, { useRef, useState } from "react";
import { Form, Button, Card, Alert, Navbar } from "react-bootstrap";
import { useAuth } from "../contexts/AuthContext";
import { Link, useHistory } from "react-router-dom";
import CenteredContainer from "./CenteredContainer";
import logo from "../images/logomain.png";
import 'react-notifications/lib/notifications.css';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import NavbarComponent from "./turfbooking/Navbar";
import Footer from "./turfbooking/Footer";

import "./Login.css";

export default function Login() {
  const emailRef = useRef();
  const passwordRef = useRef();
  const { login } = useAuth();
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const history = useHistory();

  async function handleSubmit(e) {
    e.preventDefault();

    try {
      setError("");
      setLoading(true);
      await login(emailRef.current.value, passwordRef.current.value);
      history.push("/");
      // NotificationManager.success('Logged In Successfully', 'Success', 1200);
    } catch {
      setError("Failed to log in");
    }
    setLoading(false);
  }
  document.title = "Login Form"
  return (
    <div className="Login__background">
      <NavbarComponent/>
      <CenteredContainer>
        <Card style={{backgroundColor:"#1e2e5f"}}>
          <Card.Body>
            <h2 className="text-center mb-4 login-color" style={{color:"#8e96af"}}>Log In</h2>
            {error && <Alert variant="danger">{error}</Alert>}
            <Form onSubmit={handleSubmit}>
              <Form.Group id="email">
                <Form.Label style={{color:"#8e96af"}}>Email</Form.Label>
                <Form.Control type="email" ref={emailRef} required />
              </Form.Group>
              <Form.Group id="password">
                <Form.Label style={{color:"#8e96af"}}>Password</Form.Label>
                <Form.Control type="password" ref={passwordRef} required />
              </Form.Group>
              <Button disabled={loading}  className="w-100" type="submit">
                Log In
              </Button>
            </Form>
            <div className="w-100 text-center mt-3">
              <Link to="/forgot-password" style={{color:"#8e96af"}}>Forgot Password?</Link>
            </div>
          </Card.Body>
        </Card>
        <div className="w-100 text-center mt-2">
          Need an account? <Link to="/signup" style={{color:"#8e96af"}}>Sign Up</Link>
        </div>
        <NotificationContainer/>
      </CenteredContainer>
      <Footer/>
    </div>
  );
}
