import React, { useRef, useState, useEffect } from "react";
import { Form, Button, Card, Alert, Navbar } from "react-bootstrap";
import { useAuth} from "../contexts/AuthContext";
import { Link, useHistory } from "react-router-dom";
import CenteredContainer from "./CenteredContainer";
import logo from "../images/logomain.png";
import 'react-notifications/lib/notifications.css';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import NavbarComponent from "./turfbooking/Navbar";
import Footer from "./turfbooking/Footer";

export default function Signup() {
  const emailRef = useRef();
  const nameRef = useRef();
  const contactRef = useRef();
  const passwordRef = useRef();
  const passwordConfirmRef = useRef();
  const { signup, currentUser, logout } = useAuth();
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const history = useHistory();

  async function handleSubmit(e) {
    e.preventDefault();

    if (passwordRef.current.value !== passwordConfirmRef.current.value) {
      return setError("Passwords do not match");
    }

    try {
      setError("");
      setLoading(true);
      await signup(emailRef.current.value, passwordRef.current.value);
      await logout();
      history.push("/login");
      NotificationManager.success('Account Created Successfully', 'Success', 1200);
    } catch {
      setError("Failed to log in");
    }

    setLoading(false);
  }

  useEffect(() => {
    if (currentUser) {
      console.log("ok");
      currentUser.updateProfile({
        displayName: nameRef.current.value,
        phoneNumber: contactRef.current.value,
      });
    }
  }, [currentUser]);
  document.title = "Register Form"
  return (
    <div className="Login__background">
      <NavbarComponent/>
      <CenteredContainer>
        <Card style={{backgroundColor:"#1e2e5f"}}>
          <Card.Body>
            <h2 className="text-center mb-4" style={{color:"#8e96af"}}>Sign Up</h2>
            {error && <Alert variant="danger">{error}</Alert>}
            <Form onSubmit={handleSubmit}>
              <Form.Group id="Name">
                <Form.Label style={{color:"#8e96af"}}>Name</Form.Label>
                <Form.Control type="text" ref={nameRef} required />
              </Form.Group>
              <Form.Group id="Contact">
                <Form.Label style={{color:"#8e96af"}}>Contact No.</Form.Label>
                <Form.Control type="text" ref={contactRef} required />
              </Form.Group>
              <Form.Group id="email">
                <Form.Label style={{color:"#8e96af"}}>Email</Form.Label>
                <Form.Control type="email" ref={emailRef} required />
              </Form.Group>
              <Form.Group id="password">
                <Form.Label style={{color:"#8e96af"}}>Password</Form.Label>
                <Form.Control type="password" ref={passwordRef} required />
              </Form.Group>
              <Form.Group id="password-confirm">
                <Form.Label style={{color:"#8e96af"}}>Password Confirmation</Form.Label>
                <Form.Control
                  type="password"
                  ref={passwordConfirmRef}
                  required
                />
              </Form.Group>
              <Button disabled={loading} className="w-100" type="submit">
                Sign Up
              </Button>
            </Form>
          </Card.Body>
        </Card>
        <div className="w-100 text-center mt-2">
          Already have an account? <Link to="/login" style={{color:"#8e96af"}}>Log In</Link>
        </div>
      </CenteredContainer>
      <Footer/>
      <NotificationContainer/>
    </div>
  );
}
