import React, { useState }from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import logo from "../../images/logomain.png";
import { library } from "@fortawesome/fontawesome-svg-core";
import { Navbar, Nav, NavDropdown, MenuItem } from "react-bootstrap";
import "./Navbar.css";
import { Link, useHistory } from "react-router-dom";
import {useAuth } from "../../contexts/AuthContext";
import 'react-notifications/lib/notifications.css';
import {NotificationContainer, NotificationManager} from 'react-notifications';

export default function NavbarComponent() {
  const {currentUser, logout} = useAuth();
  const history = useHistory();
  const [error, setError] = useState("");
  
  async function handleLogout() {
    setError("");

    try {
      await logout();
      // NotificationManager.success('Logged Out Successfully', 'Success', 1200);
      history.push("/");
    } catch {
      setError("Failed to log out");
    }
  }

  const onHandleLogout = () =>{
    console.log("logout done");
  }

  return (
    <>
      <div className="text-transform" style={{ fontSize: "120%" }}>
        <Navbar style={{backgroundColor:"#1e2e5f",height:"100px"}} expand="lg" variant="dark" margin="0">
          <Navbar.Brand as={Link} to="/">
            {/*<img src={logo} width="150"></img>*/}
            <h1><a style={{color:"#DFBD6A", marginLeft:"16px"}} href="/">Royal Turf</a></h1>
          </Navbar.Brand>

          <Nav className="mr-auto" style={{textAlign:"center",margin:"0 auto"}}>
            <Nav.Link as={Link} to="/" className="nav">
              <h4>Home</h4>
            </Nav.Link>
            <Nav.Link as={Link} to="/about" className="nav">
              <h4>About us</h4>
            </Nav.Link>
            <Nav.Link as={Link} to="/TurfList" className="nav">
              <h4>Turf List</h4>
            </Nav.Link>
          </Nav>
              {currentUser && currentUser.email ? 
                <NavDropdown eventKey={4} noCaret={true} style={{color:"#8e96af",marginRight:"30px"}} onClick={(e) => e.stopPropagation()}
                  title={[<span className="user-name"><h4 style={{float:"left",color:"#8e96af"}}>{currentUser.email}</h4></span>,
                    <span key="angle-down" className="angle-down s7-angle-down" style={{color:"#8e96af"}}></span>]} id="basic-nav-dropdown">
                        <NavDropdown.Item eventKey={4.5} onClick={handleLogout}><span className="icon s7-power"></span>Sign Out</NavDropdown.Item>
                </NavDropdown>
              : <Nav.Link as={Link} to="/login" className="nav" style={{color:"#8e96af",marginRight:"40px"}}>
                <h4>Login/Signup</h4>
                </Nav.Link>
              }
        </Navbar>
      </div>
      <NotificationContainer/>
    </>
  );
}
