import React, {Component} from "react";
import turfImg from "./../images/Nerul1.jpg";
import turfImg1 from "./../images/Nerul2.jpg";
import turfImg2 from "./../images/Nerul3.jpg";
import turfImg3 from "./../images/Nerul4.jpg";
import turfImg4 from "./../images/Thane1.jpg";
import turfImg5 from "./../images/Thane2.jpg";
import turfImg6 from "./../images/Thane3.jpg";
import turfImg7 from "./../images/Thane4.jpg";
import turfImg8 from "./../images/Dadar1.jpg";
import turfImg9 from "./../images/Dadar2.JPG";
import turfImg10 from "./../images/Dadar3.jpeg";
import turfImg11 from "./../images/Dadar4.png";
import turfImg12 from "./../images/Andheri1.jpeg";
import turfImg13 from "./../images/Andheri2.png";
import turfImg14 from "./../images/Andheri3.PNG";
import turfImg15 from "./../images/Andheri4.PNG";
import { Button, Container, Row,Col } from "react-bootstrap";
import "./Home.css";
import TurfListMain from "./TurfListMain";
import TimeSlot from "./TimeSlot";
import {useAuth } from "../contexts/AuthContext";
import NavbarComponent from "./turfbooking/Navbar";
import Footer from "./turfbooking/Footer";


// function GetcurrentUser() {
//   currentUser = useAuth().currentUser;
//   let email = currentUser.email;
//   console.log("email",email);
//   console.log("user",currentUser);
// }
const thaneDesc = "Strikers Arena Behind Lodha Amara, Kolshet Industrial Area, Thane West, Thane, Maharashtra 400607 ";
const nerulDesc = "Urban Sports Zone, Nerul East, Sector 3, Nerul, Navi Mumbai, Maharashtra 400706 ";
const andheriDesc = "Urban Sports-Andheri, Gate No 4, Backside Road, Jankidevi School Rd, Four Bungalows, Andheri West, Mumbai, Maharashtra 400053";
const dadarDesc = "DrAntonio Da Silva Turf Near Kabutar khana, Dadar West, Dadar, Mumbai, Maharashtra 400028";

const CustomFilterUser = ({children}) => {
  const currentUser = useAuth().currentUser;
  return children(currentUser);
}
export default class TurfList extends Component {
  constructor(props) {
    super(props);

    this.state = {
      showBookingView:false
    }
  };

  componentWillMount(){
this.handleBackBrowserClick();
}

componentDidMount(){
  document.title = "TURF LIST"
}
handleBackBrowserClick =() =>{
  window.history.pushState(null, null, '');
  window.addEventListener('popstate', function(event) {
    window.location.assign("/");
});
}
  
  handleViewClick = (mainImg, name, location, currentUser) =>{
    if(currentUser && currentUser.email){
    this.turfDetails = {
      mainImg:mainImg,
      name:name,
      location:location
    }
    this.setState({
      showBookingView:true
    });}else{
      window.location.href = "/login";
    }
  }
  render(){
  return (
    this.state.showBookingView ?
    <CustomFilterUser>
    {(currentUser)=><TimeSlot turfDetails={this.turfDetails} userEmail={currentUser && currentUser.email ? currentUser.email : null}/>}
    </CustomFilterUser>
    :
    <div className="location__background">
      <NavbarComponent/>
      <Container>
        <Row>
          <Col md={6}>
            <TurfListMain name="Nerul" mainImg={[turfImg,turfImg1,turfImg2,turfImg3]} location="Nerul" turfDesc={nerulDesc} handleViewClick={this.handleViewClick}/>
          </Col>
          <Col md={6}>
            <TurfListMain name="Thane" mainImg={[turfImg4,turfImg5,turfImg6,turfImg7]} location="Nerul" turfDesc={thaneDesc} handleViewClick={this.handleViewClick}/>
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <TurfListMain name="Dadar" mainImg={[turfImg8,turfImg9,turfImg10,turfImg11]} location="Nerul" turfDesc={dadarDesc} handleViewClick={this.handleViewClick}/>
          </Col>
          <Col md={6}>
            <TurfListMain name="Andheri" mainImg={[turfImg12,turfImg13,turfImg14,turfImg15]} location="Nerul" turfDesc={andheriDesc} handleViewClick={this.handleViewClick}/>
          </Col>
        </Row>
      </Container>
      <Footer/>
    </div>
  );
  }
}