import React, { Component } from "react";
import { Table, Tr, Td } from "reactable";
import Select from "react-select";
import { Button } from "react-bootstrap";
import firebase from "firebase";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import moment from "moment";
import 'react-notifications/lib/notifications.css';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import "./Home.css";
import "./Login.css";

export default class Payment extends Component {
  componentDidMount(){
    document.title = "Payment Confirmation"
  }
  render(){
    return(
      <div className="payment__background">
        <main class="page payment-page payment__background">
    <section class="payment-form dark">
      <div class="container">
        <div class="block-heading" style={{color:"white"}}>
          <h1>Payment</h1>
          <p>Enter your information below to confirm your purchase through Royal Turf</p>
        </div>
        <form>
          <div class="products">
            <h3 class="title">Checkout</h3>
            <div class="item">
              <span class="price">{null}</span>
              <p class="item-name">{this.props.turfName}</p>
              <p class="item-description">Turf Location</p>
            </div>
            <div class="item">
              <span class="price">₹1200</span>
              <p class="item-name">{this.props.timeSlot}</p>
              <p class="item-description">Turf Time Slot</p>
            </div>
            <div class="total">Total<span class="price">₹1200 </span></div>
          </div>
          <div class="card-details">
            <h3 class="title">Payment Details</h3>
            <div class="row">
              <div class="form-group col-sm-7">
                <label for="card-holder">Card Holder Name</label>
                <input id="card-holder" type="text" class="form-control" placeholder="Card Holder Name" aria-label="Card Holder" aria-describedby="basic-addon1"/>
              </div>
              <div class="form-group col-sm-5">
                <label for="">Expiration Date</label>
                <div class="input-group expiration-date">
                  <input type="text" class="form-control" placeholder="MM" aria-label="MM" aria-describedby="basic-addon1" value="12"/>
                  <span class="date-separator">/</span>
                  <input type="text" class="form-control" placeholder="YY" aria-label="YY" aria-describedby="basic-addon1" value="26"/>
                </div>
              </div>
              <div class="form-group col-sm-8">
                <label for="card-number">Card Number</label>
                <input id="card-number" type="text" class="form-control" placeholder="Card Number" aria-label="Card Holder" aria-describedby="basic-addon1" value="xxxx-xxxx-xxxx-xxxx"/>
              </div>
              <div class="form-group col-sm-4">
                <label for="cvc">CVC</label>
                <input id="cvc" type="text" class="form-control" placeholder="CVC" aria-label="Card Holder" aria-describedby="basic-addon1" value="xxx"/>
              </div>
              <div class="form-group col-sm-12">
                <button type="button" class="btn btn-primary btn-block" onClick={this.props.confirmPayment}>Pay Now</button>
              </div>
            </div>
          </div>
        </form>
      </div>
    </section>
  </main>
      </div>
    )
  }
}