import React, {Component} from "react";
import { Button, Container, Row,Col } from "react-bootstrap";
import {useAuth } from "../contexts/AuthContext";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import "./Home.css";

const responsive = {
  desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 1,
    slidesToSlide: 1 // optional, default to 1.
  },
  tablet: {
    breakpoint: { max: 1024, min: 464 },
    items: 1,
    slidesToSlide: 1 // optional, default to 1.
  },
  mobile: {
    breakpoint: { max: 464, min: 0 },
    items: 1,
    slidesToSlide: 1 // optional, default to 1.
  }
};

// import "./card.css";


const CustomFilterUser = ({children}) => {
  const currentUser = useAuth().currentUser;
  return children(currentUser);
}

export default class TurfListMain extends Component {
  render(){
    const {mainImg, name, location} = this.props;
    const that = this;
    return (
      <div>
{/*        <div className="Turf">
          <h2>{this.props.name}</h2>
          <img width="400px" src={this.props.mainImg} alt="bg4" />
          <p>
            <b>Location:</b>- {this.props.location}
          </p>
          <CustomFilterUser>{(currentUser)=><Button className="" onClick={()=>this.props.handleViewClick(mainImg, name, location,currentUser)}>View</Button>}</CustomFilterUser>
        </div>*/}
        <div class="recipe-card">

  <aside>

    {/*<img src={this.props.mainImg} alt={this.props.name} width="400px" height="400px"/>*/}
    <Carousel
      swipeable={false}
      draggable={false}
      responsive={responsive}
      ssr={true} // means to render carousel on server-side.
      infinite={true}
      keyBoardControl={true}
      customTransition="all .5"
      containerClass="carousel-container"
      removeArrowOnDeviceType={["tablet", "mobile"]}
      dotListClass="custom-dot-list-style"
      itemClass="carousel-item-padding-40-px"
    >
      {this.props.mainImg.map((imgUrl)=>{
        return <div className="img-turf"><img src={imgUrl} alt={that.props.name} /></div>;
      })
      }

    </Carousel>


    {/*<a href="#" class="button"><i class="fa fa-play" aria-hidden="true"></i></a>*/}

  </aside>

  <article>

    <div style={{marginTop:"10px"}}><h2 style={{float:"left",paddingRight:"10px",color:"white"}}>{this.props.name}</h2>
    <CustomFilterUser>{(currentUser)=><Button style={{backgroundColor:"#1e2e5f",color:"white",borderColor:"#1e2e5f"}} onClick={()=>this.props.handleViewClick(mainImg, name, location,currentUser)}>Explore</Button>}</CustomFilterUser>
    </div>
    <strong><p style={{float:"right"}}>{this.props.turfDesc}</p>
    </strong>
  </article>

</div>
      </div>
    );
  }
}