import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faFacebookF,
  faGithub,
  faInstagram,
  faLinkedin,
  faTwitter,
} from "@fortawesome/free-brands-svg-icons";
import { library } from "@fortawesome/fontawesome-svg-core";
import { Navbar, Nav } from "react-bootstrap";
import "./Footer.css";
import "../framework.css";
import "../layout.css";

export default function Footer() {
  return (
   <div className="Footer" style={{backgroundColor:"#1e2e5f"}}>
      <Navbar bg="transparent" expand="sm" variant="dark" fixed="Relative">
        <Nav className="mr-auto">
          <section className="Footer_1">
            <a className="facebook" href="#!">
              <FontAwesomeIcon
                style={{ color: "white" }}
                icon={faFacebookF}
              ></FontAwesomeIcon>
            </a>

            <a className="facebook" href="#!">
              <FontAwesomeIcon
                style={{ color: "white" }}
                icon={faInstagram}
              ></FontAwesomeIcon>
            </a>
            <a className="facebook" href="#!">
              <FontAwesomeIcon
                style={{ color: "white" }}
                icon={faLinkedin}
              ></FontAwesomeIcon>
            </a>
            <a className="facebook" href="#!">
              <FontAwesomeIcon
                style={{ color: "white" }}
                icon={faGithub}
              ></FontAwesomeIcon>
            </a>
            <a className="facebook" href="#!">
              <FontAwesomeIcon
                style={{ color: "white" }}
                icon={faTwitter}
              ></FontAwesomeIcon>
            </a>
          </section>
        </Nav>
      </Navbar>
    </div>

  );
}
