import React, { Component } from "react";
import { Table, Tr, Td } from "reactable";
import Select from "react-select";
import { Button, Container, Row,Col } from "react-bootstrap";
import firebase from "firebase";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import moment from "moment";
import 'react-notifications/lib/notifications.css';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import Payment from "./Payment";
import NavbarComponent from "./turfbooking/Navbar";
import Footer from "./turfbooking/Footer";
import "./Login.css";
import CancelModal from "./CancelModal";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";

const responsive = {
  desktop: {
    breakpoint: { max: 2000, min: 1024 },
    items: 1,
    slidesToSlide: 1 // optional, default to 1.
  },
  tablet: {
    breakpoint: { max: 1024, min: 464 },
    items: 1,
    slidesToSlide: 1 // optional, default to 1.
  },
  mobile: {
    breakpoint: { max: 464, min: 0 },
    items: 1,
    slidesToSlide: 1 // optional, default to 1.
  }
};

export default class TimeSlot extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isReload: false,
      selectedDate: new Date(),
      isButtonDisabled:false,
      showPaymentPage:null,
      showHide:false
    };
      
      this.timeSlot="08:00 - 09:00";
      this.turfData = [
      {
        TimeSlot: "08:00 - 09:00",
        Status: "Available",
        Action: this.ButtonComponent(),
      },
      {
        TimeSlot: "09:00 - 10:00",
        Status: "Available",
        Action: this.ButtonComponent(),
      },
      {
        TimeSlot: "10:00 - 11:00",
        Status: "Available",
        Action: this.ButtonComponent(),
      },
      {
        TimeSlot: "11:00 - 12:00",
        Status: "Available",
        Action: this.ButtonComponent(),
      },
      {
        TimeSlot: "12:00 - 13:00",
        Status: "Available",
        Action: this.ButtonComponent(),
      },
      {
        TimeSlot: "13:00 - 14:00",
        Status: "Available",
        Action: this.ButtonComponent(),
      },
      {
        TimeSlot: "14:00 - 15:00",
        Status: "Available",
        Action: this.ButtonComponent(),
      },
      {
        TimeSlot: "15:00 - 16:00",
        Status: "Available",
        Action: this.ButtonComponent(),
      },
      {
        TimeSlot: "16:00 - 17:00",
        Status: "Available",
        Action: this.ButtonComponent(),
      },
      {
        TimeSlot: "17:00 - 18:00",
        Status: "Available",
        Action: this.ButtonComponent(),
      },
      {
        TimeSlot: "18:00 - 19:00",
        Status: "Available",
        Action: this.ButtonComponent(),
      },
    ];

  }

  componentWillMount = () => {
    this.turfData.map((d) => {
      this.getBookingDetails(d.TimeSlot);
    });
  };

  ButtonComponent = (status, showCancelButton=false) => { 
    return(
      <div>
      <Button className="pull-left" style={{backgroundColor:"#1e2e5f",borderColor:"#1e2e5f"}}variant="primary" onClick={this.handleBooking} disabled={status && status === "Already Booked" ? true : false}>
        Book
      </Button>
      {showCancelButton ? <Button className="btn btn-danger" variant="primary" onClick={this.handleCancelClick} >
        Cancel
      </Button> : null}
      </div>
    );
  }

  handleBooking = (e, data) => {
    this.timeSlot =
      e.target.parentElement.parentElement.parentElement.firstElementChild.innerText;
      this.setState({
        showPaymentPage:true
      })
  };

  initializeBooking = (timeslot, Status) => {
    firebase
      .database()
      .ref(moment(this.state.selectedDate).format("DD/MM/YYYY") + timeslot + this.props.turfDetails.name)
      .set({
        timeslot: timeslot,
        Status: Status,
        userEmail : this.props.userEmail,
      });
      NotificationManager.success('Slot Booked Successfully', 'Success', 1200);
    this.getBookingDetails(timeslot);
  };

  getBookingDetails = (timeslot, selectedDate) => {
    const that = this;
    firebase
      .database()
      .ref(moment(this.state.selectedDate).format("DD/MM/YYYY") + timeslot + this.props.turfDetails.name)
      .on("value", (snapshot) => {
        if (snapshot && snapshot.exists()) {
          const data = snapshot.val();
          this.turfData.map((d) => {

            if (data.timeslot == d.TimeSlot) {
              const showCancelButton = data.userEmail === that.props.userEmail;
              d.Status = "Already Booked";
              d.Action = that.ButtonComponent(d.Status, showCancelButton);
            }
          });
          this.renderAgain();
        }else{
          this.turfData.map((d)=>{
            if(timeslot === d.TimeSlot){
              d.Status = "Available"
              d.Action = that.ButtonComponent(d.Status);
            }
          })
        }
      });
      this.renderAgain();
  };

  renderAgain = () => {
    this.setState({ isReload: !this.state.isReload });
  };

  handleDateChange = (value) => {
    const that = this;
    this.turfData.map((d) => {
      d.Status = "Available";
      d.Action = that.ButtonComponent();
    });
    this.setState(
      {
        selectedDate: value,
      },
      () => {
        this.turfData.map((d) => {
          this.getBookingDetails(d.TimeSlot, value);
        });
      }
    );
  };

  handleCancelClick = (e, data) => {
    this.timeSlot =
      e.target.parentElement.parentElement.parentElement.firstElementChild.innerText;
      this.handleModalShowHide();
  };

  cancelBooking = (timeslot, Status) => {
    firebase
      .database()
      .ref(moment(this.state.selectedDate).format("DD/MM/YYYY") + timeslot + this.props.turfDetails.name)
      .remove();
    this.getBookingDetails(timeslot);
    NotificationManager.success('Booking cancelled Successfully', 'Success', 1200);
  };

  confirmPayment = () => {
    this.setState({
      showPaymentPage: null
    }, () => {
      this.initializeBooking(this.timeSlot, "Booked");

    })
  }
  componentDidMount(){
    document.title = "Booking"
  }
  handleModalShowHide = (confirmCancel = false) => {
    this.setState({
      showHide: !this.state.showHide
    })
    if(confirmCancel === true){
      this.cancelBooking(this.timeSlot);
    }
  }

  getTurfRules = () => {
    let rules;
    switch (this.props.turfDetails.name) {
      case "Nerul":
        rules = <strong><p style={{color:"#ccc",marginTop:"10px"}}>
                <b><h5>Rules : </h5></b>  1. No Smoking
                <br/>2. No Alcohol Consumption<br/>
                <br/>
                  Price : Rs. 1200 /hr (Non-negotiable)
                <br/>
                <br/>Maximum Individuals allowed per Booking session is 10<br/>
              </p>
              </strong>;
        break;
      case "Thane":
        rules = <strong><p style={{color:"#ccc",marginTop:"10px"}}>
                <b><h5>Rules : </h5></b> 1. No Smoking
                <br/>2. No Alcohol Consumption<br/>
                <br/>
                  Price : Rs. 1200 /hr (Non-negotiable)
                <br/>
                <br/>Maximum Individuals allowed per Booking session is 10<br/>
              </p>
              </strong>;
        break;
      case "Andheri":
        rules = <strong><p style={{color:"#ccc",marginTop:"10px"}}>
                <b><h5>Rules : </h5></b> 1. No Smoking
                <br/>2. No Alcohol Consumption<br/>
                <br/>
                  Price : Rs. 1200 /hr (Non-negotiable)
                <br/>
                <br/>Maximum Individuals allowed per Booking session is 10<br/>
              </p>
              </strong>;
        break;
      case "Dadar":
        rules = <strong><p style={{color:"#ccc",marginTop:"10px"}}>
                <b><h5>Rules : </h5></b> 1. No Smoking
                <br/>2. No Alcohol Consumption<br/>
                <br/>
                  Price : Rs. 1200 /hr (Non-negotiable)
                <br/>
                <br/>Maximum Individuals allowed per Booking session is 10<br/>
              </p>
              </strong>;
    }
    return rules;
  }
  render() {
    return (
      this.state.showPaymentPage ?
      <Payment confirmPayment={this.confirmPayment} timeSlot={this.timeSlot} turfName={this.props.turfDetails.name}/>
      :<div className="timeslot__background" style={{height:"1052px"}}>
        <NavbarComponent/>
        <CancelModal handleModalShowHide={this.handleModalShowHide} showHide={this.state.showHide} timeSlot={this.timeSlot} turfName={this.props.turfDetails.name}/>
        <Container>
        <Row>
          <Col md={6}>
            <div style={{marginLeft:"30px",marginTop:"10px"}}>
              <h2 style={{color:"#ccc"}}>{this.props.turfDetails.name}</h2>
              {/*<img width="400px" src={this.props.turfDetails.mainImg} alt="bg4" />*/}
<aside>
            <Carousel
              swipeable={false}
              draggable={false}
              responsive={responsive}
              ssr={true} // means to render carousel on server-side.
              infinite={true}
              keyBoardControl={true}
              customTransition="all .5"
              containerClass="carousel-container"
              removeArrowOnDeviceType={["tablet", "mobile"]}
              dotListClass="custom-dot-list-style"
              itemClass="carousel-item-padding-40-px"
            >
            {this.props.turfDetails.mainImg.map((imgUrl)=>{
              return <div className="img-turf"><img src={imgUrl} alt="bg4"/></div>;
            })
            }
            </Carousel>
</aside>
            {this.getTurfRules()}
            </div>
    
          </Col>
          <Col md={6}>
            <div style={{marginTop:"10px"}}>
          <div>
          <span style={{fontSize:"20px",color:"#ccc",float:"left"}}><strong>SELECT DATE : &nbsp;</strong></span>
          <DatePicker
            selected={this.state.selectedDate}
            onChange={this.handleDateChange} //only when value has changed
            minDate={moment().toDate()}
            dateFormat={"dd/MM/yyyy"}
          />
        </div>
        <div style={{ width: "80%",marginTop:"12px"}}>
          <Table className="table" id="table" data={this.turfData}></Table>
        </div>
        <NotificationContainer/>
      </div>
          </Col>
        </Row>
        </Container>
    <Footer/>
    </div>
    );
  }
}
